// Pagination
.swiper-pagination, .swiper-pagination-inner {
  // position: absolute;
  text-align: center;
  transition: 300ms opacity;
  // transform: translate3d(0,0,0);
  // z-index: 10;
  margin: 10px auto 0;
  // display: flex;
  // justify-content: center;
  // align-items: center;
  // height: 40px;
  
  &.swiper-pagination-hidden {
    opacity: 0;
  }
}
/* Common Styles */
.swiper-pagination-fraction, .swiper-pagination-custom, .swiper-container-horizontal > .swiper-pagination-bullets{
  // bottom: 10px;
  // left: 0;
  width: 100%;
}
/* Bullets */
.swiper-pagination-bullets-dynamic {
  // overflow: hidden;
  // width: 100% !important;
  font-size: 0;
  .swiper-pagination-bullet {
    display: none;
  }
  .swiper-pagination-bullet-active, .swiper-pagination-bullet-active-main, .swiper-pagination-bullet-active-prev, .swiper-pagination-bullet-active-next {
    display: inline-flex;
  }
  .swiper-pagination-bullet-active-prev-prev, .swiper-pagination-bullet-active-next-next {
    display: inline-flex;
    &::before{
      transform: scale(0.66);
    }
  }
}
.swiper-pagination-bullet {
  width: 12px;
  height: 12px;
  display: inline-flex;
  opacity: 0.4;
  outline: none;
  align-items: center;
  justify-content: center;
  &::before{
    content: "";
    display: inline-block;
    width: 7px;
    height: 7px;
    border-radius: $br-bullet;
    background: $c-bullet-dark;
  }

  @at-root button#{&} {
    border: none;
    margin: 0;
    padding: 0;
    box-shadow: none;
    appearance: none;
  }
  .swiper-pagination-clickable & {
    cursor: pointer;
  }
}
.swiper-pagination-bullet-active {
  opacity: 1;
  // background: var(--swiper-pagination-color, var(--swiper-theme-color));
}

