/* a11y */
.swiper-container .swiper-notification {
  position: absolute;
  left: 0;
  top: 0;
  pointer-events: none;
  opacity: 0;
  z-index: -1000;
}
  

  /* Scrollbar */
.swiper-scrollbar {
    // border-radius: 10px;
    position: relative;
    background: $c-light;
    
    .swiper-container-horizontal > & {
      position: absolute;
      left: 40px;
      bottom: 0;
      z-index: $z-low;
      height: 2px;
      width: calc(100% - 40px);
    }
    // .swiper-container-vertical > & {
    //   position: absolute;
    //   right: 3px;
    //   top: 1%;
    //   z-index: 50;
    //   width: 5px;
    //   height: 98%;
    // }
}
.swiper-scrollbar-drag {
  height: 100%;
  width: 100%;
  position: relative;
  background: $c-dark;
  // border-radius: 10px;
  left: 0;
  top: 0;
  cursor: pointer;
}
.swiper-scrollbar-cursor-drag {
  cursor: move;
}
.swiper-scrollbar-lock {
  display: none;
}

// Navigation
.swiper-button {
  &-prev,
  &-next {
    position: absolute;
    top: 50%;
    z-index: $z-lower;
    width: 32px;
    height: 32px;
    margin-top: -16px;
    @extend .svg;
    @extend .svg-swiper-next-dark;
    outline: none;
  }
  &-prev {
    left: 40px;
    transform: scale(-1);
  }
  &-next {
    right: 40px;
  }

  &-disabled {
    opacity: .5;
  }
}

// Zoom
.swiper-zoom-container {
  width: 100%;
  height: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
  text-align: center;
  cursor: zoom-in;

  > img, > svg, > canvas {
    max-width: 100%;
    max-height: 100%;
    object-fit: contain;
  }
}

.swiper-slide-zoomed .swiper-zoom-container{
  cursor: move;
}

// THEME
.swiper-theme-light {
  .swiper-pagination-bullet {
    &::before{
      background: $c-white;
    }
  }
  .swiper-button{
    &-prev,
    &-next {
      @extend .svg-swiper-next;
    }
  }
}