//ABSTRACTS
// @import 'abstracts/variables';
// Directories
$dir-images: 'assets/images/';
$dir-fonts: 'assets/css/fonts/';

// Fonts
$font0: 'arial', 'sans-serif';

// 1rem default 16px = xx/16
$px-to-rem: 16;

// z-index
$z-zero:0;
$z-lower:1;
$z-low:2;
$z-normal:3;
$z-supnormal:4;
$z-high:5;
$z-higher:6;
$z-highest:7;
$z-super:8;
$z-ultra:9;
$z-master:10;

// Breakpoints
$breakpoint0: 0;
$breakpoint1-2: 480px;
$breakpoint1: 576px;
$breakpoint2: 768px;
$breakpoint3: 1025px;
$breakpoint4: 1300px;

// Transitions
$transition-ease-in: cubic-bezier(0.550, 0.055, 0.675, 0.190);
$transition-ease-out: cubic-bezier(0.215, 0.610, 0.355, 1.000);
$transition-ease-in-out: cubic-bezier(0.645, 0.045, 0.355, 1.000);

// Containers
$containers: (540, 720, 880, 1200);
$container-padding-x: 20px;

// Misc in Site
$scroll-width-lg: 16px;
$header-height-mobile: 51px;
// $header-height-desktop: 61px;

@import 'abstracts/brand-settings';
@import 'abstracts/mixins';
@import 'abstracts/app-shell-source';

//VENDORS
@import 'vendors/bootstrap-custom-base';
// stylelint-disable at-rule-no-vendor-prefix, declaration-no-important, selector-no-qualifying-type, property-no-vendor-prefix

// Reboot
//
// Normalization of HTML elements, manually forked from Normalize.css to remove
// styles targeting irrelevant browsers while applying new styles.
//
// Normalize is licensed MIT. https://github.com/necolas/normalize.css


// Document
//
// 1. Change from `box-sizing: content-box` so that `width` is not affected by `padding` or `border`.
// 2. Change the default font family in all browsers.
// 3. Correct the line height in all browsers.
// 4. Prevent adjustments of font size after orientation changes in IE on Windows Phone and in iOS.
// 5. Change the default tap highlight to be completely transparent in iOS.

*,
*::before,
*::after {
  box-sizing: border-box; // 1
}

html {
  font-family: sans-serif; // 2
  line-height: 1.15; // 3
  -webkit-text-size-adjust: 100%; // 4
  -webkit-tap-highlight-color: rgba($black, 0); // 5
}

// Shim for "new" HTML5 structural elements to display correctly (IE10, older browsers)
// TODO: remove in v5
// stylelint-disable-next-line selector-list-comma-newline-after
article, aside, figcaption, figure, footer, header, hgroup, main, nav, section {
  display: block;
}

// Body
//
// 1. Remove the margin in all browsers.
// 2. As a best practice, apply a default `background-color`.
// 3. Set an explicit initial text-align value so that we can later use
//    the `inherit` value on things like `<th>` elements.

body {
  margin: 0; // 1
  font-family: $font-family-base;
  @include font-size($font-size-base);
  font-weight: $font-weight-base;
  line-height: $line-height-base;
  color: $body-color;
  text-align: left; // 3
  background-color: $body-bg; // 2
}

// Future-proof rule: in browsers that support :focus-visible, suppress the focus outline
// on elements that programmatically receive focus but wouldn't normally show a visible
// focus outline. In general, this would mean that the outline is only applied if the
// interaction that led to the element receiving programmatic focus was a keyboard interaction,
// or the browser has somehow determined that the user is primarily a keyboard user and/or
// wants focus outlines to always be presented.
//
// See https://developer.mozilla.org/en-US/docs/Web/CSS/:focus-visible
// and https://developer.paciellogroup.com/blog/2018/03/focus-visible-and-backwards-compatibility/
[tabindex="-1"]:focus:not(:focus-visible) {
  outline: 0;
}


// Content grouping
//
// 1. Add the correct box sizing in Firefox.
// 2. Show the overflow in Edge and IE.

hr {
  box-sizing: content-box; // 1
  height: 0; // 1
  overflow: visible; // 2
}


//
// Typography
//

// Remove top margins from headings
//
// By default, `<h1>`-`<h6>` all receive top and bottom margins. We nuke the top
// margin for easier control within type scales as it avoids margin collapsing.
// stylelint-disable-next-line selector-list-comma-newline-after
h1, h2, h3, h4, h5, h6 {
  margin-top: 0;
  margin-bottom: $headings-margin-bottom;
}

// Reset margins on paragraphs
//
// Similarly, the top margin on `<p>`s get reset. However, we also reset the
// bottom margin to use `rem` units instead of `em`.
p {
  margin-top: 0;
  margin-bottom: $paragraph-margin-bottom;
}

// Abbreviations
//
// 1. Duplicate behavior to the data-* attribute for our tooltip plugin
// 2. Add the correct text decoration in Chrome, Edge, IE, Opera, and Safari.
// 3. Add explicit cursor to indicate changed behavior.
// 4. Remove the bottom border in Firefox 39-.
// 5. Prevent the text-decoration to be skipped.

abbr[title],
abbr[data-original-title] { // 1
  text-decoration: underline; // 2
  text-decoration: underline dotted; // 2
  cursor: help; // 3
  border-bottom: 0; // 4
  text-decoration-skip-ink: none; // 5
}

address {
  margin-bottom: 1rem;
  font-style: normal;
  line-height: inherit;
}

ol,
ul,
dl {
  margin-top: 0;
  margin-bottom: 1rem;
}

ol ol,
ul ul,
ol ul,
ul ol {
  margin-bottom: 0;
}

dt {
  font-weight: $dt-font-weight;
}

dd {
  margin-bottom: .5rem;
  margin-left: 0; // Undo browser default
}

blockquote {
  margin: 0 0 1rem;
}

b,
strong {
  font-weight: $font-weight-bolder; // Add the correct font weight in Chrome, Edge, and Safari
}

small {
  @include font-size(80%); // Add the correct font size in all browsers
}

//
// Prevent `sub` and `sup` elements from affecting the line height in
// all browsers.
//

sub,
sup {
  position: relative;
  @include font-size(75%);
  line-height: 0;
  vertical-align: baseline;
}

sub { bottom: -.25em; }
sup { top: -.5em; }


//
// Links
//

a {
  color: $link-color;
  text-decoration: $link-decoration;
  background-color: transparent; // Remove the gray background on active links in IE 10.

  @include hover() {
    color: $link-hover-color;
    text-decoration: $link-hover-decoration;
  }
}

// And undo these styles for placeholder links/named anchors (without href).
// It would be more straightforward to just use a[href] in previous block, but that
// causes specificity issues in many other styles that are too complex to fix.
// See https://github.com/twbs/bootstrap/issues/19402

a:not([href]) {
  color: inherit;
  text-decoration: none;

  @include hover() {
    color: inherit;
    text-decoration: none;
  }
}


//
// Code
//

pre,
code,
kbd,
samp {
  font-family: $font-family-monospace;
  @include font-size(1em); // Correct the odd `em` font sizing in all browsers.
}

pre {
  // Remove browser default top margin
  margin-top: 0;
  // Reset browser default of `1em` to use `rem`s
  margin-bottom: 1rem;
  // Don't allow content to break outside
  overflow: auto;
  // Disable auto-hiding scrollbar in IE & legacy Edge to avoid overlap,
  // making it impossible to interact with the content
  -ms-overflow-style: scrollbar;
}


//
// Figures
//

figure {
  // Apply a consistent margin strategy (matches our type styles).
  margin: 0 0 1rem;
}


//
// Images and content
//

img {
  vertical-align: middle;
  border-style: none; // Remove the border on images inside links in IE 10-.
}

svg {
  // Workaround for the SVG overflow bug in IE10/11 is still required.
  // See https://github.com/twbs/bootstrap/issues/26878
  overflow: hidden;
  vertical-align: middle;
}


//
// Tables
//

table {
  border-collapse: collapse; // Prevent double borders
}

caption {
  padding-top: $table-cell-padding;
  padding-bottom: $table-cell-padding;
  color: $table-caption-color;
  text-align: left;
  caption-side: bottom;
}

th {
  // Matches default `<td>` alignment by inheriting from the `<body>`, or the
  // closest parent with a set `text-align`.
  text-align: inherit;
}


//
// Forms
//

label {
  // Allow labels to use `margin` for spacing.
  display: inline-block;
  margin-bottom: $label-margin-bottom;
}

// Remove the default `border-radius` that macOS Chrome adds.
//
// Details at https://github.com/twbs/bootstrap/issues/24093
button {
  // stylelint-disable-next-line property-blacklist
  border-radius: 0;
}

// Work around a Firefox/IE bug where the transparent `button` background
// results in a loss of the default `button` focus styles.
//
// Credit: https://github.com/suitcss/base/
button:focus {
  outline: 1px dotted;
  outline: 5px auto -webkit-focus-ring-color;
}

input,
button,
select,
optgroup,
textarea {
  margin: 0; // Remove the margin in Firefox and Safari
  font-family: inherit;
  @include font-size(inherit);
  line-height: inherit;
}

button,
input {
  overflow: visible; // Show the overflow in Edge
}

button,
select {
  text-transform: none; // Remove the inheritance of text transform in Firefox
}

// Set the cursor for non-`<button>` buttons
//
// Details at https://github.com/twbs/bootstrap/pull/30562
[role="button"] {
  cursor: pointer;
}

// Remove the inheritance of word-wrap in Safari.
//
// Details at https://github.com/twbs/bootstrap/issues/24990
select {
  word-wrap: normal;
}


// 1. Prevent a WebKit bug where (2) destroys native `audio` and `video`
//    controls in Android 4.
// 2. Correct the inability to style clickable types in iOS and Safari.
button,
[type="button"], // 1
[type="reset"],
[type="submit"] {
  -webkit-appearance: button; // 2
}

// Opinionated: add "hand" cursor to non-disabled button elements.
@if $enable-pointer-cursor-for-buttons {
  button,
  [type="button"],
  [type="reset"],
  [type="submit"] {
    &:not(:disabled) {
      cursor: pointer;
    }
  }
}

// Remove inner border and padding from Firefox, but don't restore the outline like Normalize.
button::-moz-focus-inner,
[type="button"]::-moz-focus-inner,
[type="reset"]::-moz-focus-inner,
[type="submit"]::-moz-focus-inner {
  padding: 0;
  border-style: none;
}

input[type="radio"],
input[type="checkbox"] {
  box-sizing: border-box; // 1. Add the correct box sizing in IE 10-
  padding: 0; // 2. Remove the padding in IE 10-
}


textarea {
  overflow: auto; // Remove the default vertical scrollbar in IE.
  // Textareas should really only resize vertically so they don't break their (horizontal) containers.
  resize: vertical;
}

fieldset {
  // Browsers set a default `min-width: min-content;` on fieldsets,
  // unlike e.g. `<div>`s, which have `min-width: 0;` by default.
  // So we reset that to ensure fieldsets behave more like a standard block element.
  // See https://github.com/twbs/bootstrap/issues/12359
  // and https://html.spec.whatwg.org/multipage/#the-fieldset-and-legend-elements
  min-width: 0;
  // Reset the default outline behavior of fieldsets so they don't affect page layout.
  padding: 0;
  margin: 0;
  border: 0;
}

// 1. Correct the text wrapping in Edge and IE.
// 2. Correct the color inheritance from `fieldset` elements in IE.
legend {
  display: block;
  width: 100%;
  max-width: 100%; // 1
  padding: 0;
  margin-bottom: .5rem;
  @include font-size(1.5rem);
  line-height: inherit;
  color: inherit; // 2
  white-space: normal; // 1
}

progress {
  vertical-align: baseline; // Add the correct vertical alignment in Chrome, Firefox, and Opera.
}

// Correct the cursor style of increment and decrement buttons in Chrome.
[type="number"]::-webkit-inner-spin-button,
[type="number"]::-webkit-outer-spin-button {
  height: auto;
}

[type="search"] {
  // This overrides the extra rounded corners on search inputs in iOS so that our
  // `.form-control` class can properly style them. Note that this cannot simply
  // be added to `.form-control` as it's not specific enough. For details, see
  // https://github.com/twbs/bootstrap/issues/11586.
  outline-offset: -2px; // 2. Correct the outline style in Safari.
  -webkit-appearance: none;
}

//
// Remove the inner padding in Chrome and Safari on macOS.
//

[type="search"]::-webkit-search-decoration {
  -webkit-appearance: none;
}

//
// 1. Correct the inability to style clickable types in iOS and Safari.
// 2. Change font properties to `inherit` in Safari.
//

::-webkit-file-upload-button {
  font: inherit; // 2
  -webkit-appearance: button; // 1
}

//
// Correct element displays
//

output {
  display: inline-block;
}

summary {
  display: list-item; // Add the correct display in all browsers
  cursor: pointer;
}

template {
  display: none; // Add the correct display in IE
}

// Always hide an element with the `hidden` HTML attribute (from PureCSS).
// Needed for proper display in IE 10-.
[hidden] {
  display: none;
}

@import "../../node_modules/bootstrap/scss/grid";
@import "../../node_modules/bootstrap/scss/transitions";
@import "../../node_modules/bootstrap/scss/nav";
@import "../../node_modules/bootstrap/scss/close";
@import "../../node_modules/bootstrap/scss/modal";
@import 'vendors/swiper-custom';
// @import 'vendors/nouislider-custom';
// @import 'vendors/threesixty-custom';
// @import 'vendors/pikaday-custom';

//BASE
@import 'base/typography';
@import 'base/icons';
@import 'base/icons-bg';
@import 'base/animations';
// @import 'base/global';
body{
    overflow: auto;
}

::selection {
  background: $c-primary;
  color: $c-text-light;
  text-shadow:none;
}

html {
    scroll-behavior: smooth;
    background-color: $c-bg-body;
}

img {
    max-width: 100%;
}

a {
    cursor: pointer;
    outline: none;
    text-decoration: none;
}

p{
    a{
        text-decoration: underline;
    }
}

input{
    outline: none;

    &[type=number]{
        -moz-appearance: textfield;
    }

    &::-webkit-outer-spin-button,
    &::-webkit-inner-spin-button {
        -webkit-appearance: none;
    }
    &::-ms-reveal,
    &::-ms-clear {
      display: none;
    }
}

textarea{
    resize: none;
}

.clearfix{
    &::after {
        display: block;
        clear: both;
        content: "";
    }
}

figure{
    margin:0;
}

@include button-appshell;

// CONTAINERS
[class*="container-max-"]{
    margin:0 auto;
    padding: 0 $container-padding-x;
}

.container-max-{
    @each $c in $containers{
        &#{$c}{
            max-width: #{$c + ($container-padding-x * 2)};
        }
    }
}

.ul-clear{
    padding: 0;
    margin: 0;
    list-style: none;
}

.f-input {
    $p: 10px;
    flex: 1;
    display: flex;
    align-items: center;
    background: #e5e5e5;
    border-radius: 5px;
    padding: 10px;

    .icon-holder:not(:last-child) {
        margin-right: 10px;
    }

    > span {
        color: rgba($color: $c-black, $alpha: .5);
    }

    form {
        flex: 1;
        align-self: stretch;
        display: flex;
        align-items: center;
    }
    input {
        flex: 1;
        background: inherit;
        border: none;
        padding: 0;
        line-height: 32px;
        &::placeholder {
            color: rgba($color: $c-black, $alpha: .5);
        }
    }
}

@include qty-badge-appshell;

// Energy Color Generator
a[class*="energy-"]{
    line-height: $energy-label-height;
    font-style: normal;
    text-align: center;
    display: inline-block;
    height: $energy-label-height;
    width: 25px;
    position: relative;
    background-color: currentColor;
    padding-left: 3px;
    margin-right: 9px; // Fix for ::after

    span{
        color: $c-text-light;
        font-size: $fs-energy-icon;
        position: relative;
        z-index: $z-low;
    }
    
    &::after{
        content: "";
        width: 0;
        height: 0;
        border-style: solid;
        border-color: transparent transparent transparent currentColor;
        border-width: 8.5px 0 8.5px 9px;
        position: absolute;
        top: 0;
        right: -9px;
        z-index: $z-lower;
    }
}

a.energy{
    @each $k, $v in $energy-colors{
        &-#{$k}{
            color: $v;
        }
    }
}

// @import 'layouts/elements';
//BLOCKS
@include d-elements-appshell;

// Mobile Scrollable
.scrollable-h{
    white-space: nowrap;
    overflow-x: auto;
    -webkit-overflow-scrolling: touch;
    -ms-overflow-style: -ms-autohiding-scrollbar;

    > * {
        white-space: normal;
    }

    &::-webkit-scrollbar{
        display: none;
    }
}

// Scrollable Area
.scrollable-area {
    overflow: auto;
    padding-bottom: 10px;
    width: 100%;
}

.link{
    position: relative;

    &::after{
        content: "";
        display: inline-block;
        width: 100%;
        height: 0;
        border-bottom: 1px solid currentColor;
        position: absolute;
        bottom: 1px;
        left: 0;
        font-size: 0;
        opacity: 0;
        @include transitions();
    }

    &-inline{
        position: relative;

        &::after{
            content: "";
            display: inline-block;
            width: 100%;
            height: 0;
            border-bottom: 1px solid currentColor;
            position: absolute;
            bottom: 1px;
            left: 0;
            font-size: 0;
            @include transitions();
        }
    }
}

.input{
    position: relative;
    display: inline-block;
}

.textbox{
    width: 100%;
    .wrp{
        display: block;
        position: relative;
    }
    i{
        display: inline-block;
        position: absolute;
        top: ($h-input - 32px) / 2;
        right: 0;
    }
    .timer{
        position: absolute;
        top: 0;
        right: 0;
        @include font-set(body-small);
        color: $c-primary;
    }
    input{
        height: $h-input;
        width: 100%;
        border-style: solid;
        border-color: $c-grey-light;
        border-width: 0 0 1px 0;
        padding: 0 5px;
        background: none;
        outline: none;

        &:focus{
            border-bottom-color: $c-grey-dark;
        }
        &:disabled{
            opacity: .7;
        }
    }
    label{
        @include font-set(body-small);
        margin: 0;
    }
    .input-icon{
        input{
            padding-right: 37px;
        }
    }
}

.textarea{
    width: 100%;
    textarea{
        height: $h-textarea;
        width: 100%;
        border-style: solid;
        border-color: $c-grey-light;
        border-width: 0 0 1px 0;
        padding: 25px 5px;
        background: none;
        outline: none;

        &:focus{
            border-bottom-color: $c-grey-dark;
        }
    }
    label{
        @include font-set(body-small);
        margin: 0;
    }
}

.textbox input.form-error, .form-error .textbox input, textarea.form-error, .form-error textarea{
    border-bottom-color: $c-error;
}
.form-error .textbox .wrp, .form-error .textarea .wrp{
    &::before{
        content: "";
        display: inline-block;
        @extend .svg;
        @extend .svg-input-error;
        position: absolute;
        top: ($h-input - 32px) / 2;
        right: 0;
    }
}
.form-error .textarea .wrp{
    &::before{
        top: auto;
        bottom: 0;
    }
}
.textbox input.processing, .processing .textbox input{
    background: url(#{$dir-images + 'preloader.svg'}) right center no-repeat;
    background-size: contain;
}

.checkbox, .radio, .rl-item{
    input{
        width:32px;
        height:32px;
        position: absolute;
        opacity: 0;
        z-index: 2;
        top: 0;
        left: 0;
        cursor: pointer;
    }

    label{
        display: inline-block;
        padding: 9px 0 0 32px;
        margin: 0;
        min-height: 32px;
        cursor: pointer;
    
        &::before{
            content:"";
            position:absolute;
            top: 0;
            left: 0;
        }

        &::after{
            content:"";
            position:absolute;
            top: 0;
            left: 0;
            transform: scale(0);
            @include transitions($dur: "now");
        }
    }

    input:checked + label{
        &::before{
            opacity: 0;
        }

        &::after{
            transform: scale(1);
        }
    }
}

.checkbox{
    label{
        &::before{
            @extend .svg;
            @extend .svg-check;
        }

        &::after{
            @extend .svg;
            @extend .svg-check-full;
        }
    }
}

.radio, .rl-item{
    label{
        &::before{
            @extend .svg;
            @extend .svg-radio;
        }

        &::after{
            @extend .svg;
            @extend .svg-radio-full;
        }
    }
}

// Select
.select{
    width: 100%;
    label{
        @include font-set(body-small);
        margin: 0;
    }

    .wrp{
        position: relative;
        display: inline-block;
        width: 100%;
        height: 48px;
        &::after{
            content: "";
            display: inline-block;
            @extend .svg;
            @extend .svg-arrow2-r;
            transform: rotate(90deg);
            position: absolute;
            right: 0;
            top: calc(50% - 16px);
        }
    }

    select{
        -webkit-appearance: none;
        -moz-appearance: none;
        -ms-appearance: none;
        appearance: none;
        outline: 0;
        box-shadow: none;
        background: none;
        height: 100%;
        width: 100%;
        border-style: solid;
        border-color: $c-grey-light;
        border-width: 0 0 1px 0;
        padding: 0 5px;
        cursor: pointer;
        position: absolute;
        top: 0;
        left: 0;
        z-index: $z-lower;

        &::-ms-expand {
            display: none;
        }

        &:focus{
            border-bottom-color: $c-grey-dark;
        }
    }
}

// Forms
.form{
    &-rows {
        .mt-40 {
            margin-top: 40px;
        }
    }
    &-row{
        margin-bottom: 20px;
        &-info {
            a {
                text-decoration: underline;
            }
            &-col {
                display: flex;
                align-items: flex-start;
                .icon {
                    margin-right: 10px;
                }
            }
        }
    }
    &-error-msg{
        margin-top: 5px;
        @include font-set(body-small);
        color: $c-error;
    }
    &-result{
        justify-content: center;
        flex-direction: column;
        height: 100%;
        &-title{
            margin: 20px 0 10px;
            text-align: center;
            @include font-set(h);
        }
        &-text{
            text-align: center;
            p{
                margin-bottom: 20px;
            }
            .icon{
                color: $c-success;
            }
        }
        &-button{
            margin-top: auto;
            .btn{
                width: 100%;
            }
        }
    }
    &-col{
        &-2{
            &-1{
                padding-right: 7px;
            }
            &-2{
                padding-left: 7px;
            }
        }
        &-3{
            &-1{
                padding-right: 7px;
            }
            &-2{
                padding-left: 7px;
                padding-right: 7px;
            }
            &-3{
                padding-left: 7px;
