// Accordions
.acc-{
    &item{
        border-top: 1px solid $c-grey-light;
        position: relative;

        > button{
            display: block;
            width: 100%;
            padding: 24px 42px 24px 0;
            position: relative;

            &::after{
                content: "";
                display: inline-block;
                @extend .svg;
                @extend .svg-plus;
                position: absolute;
                top: calc(50% - 16px);
                right: 0;
            }
        }
    }

    &badge {
        position: absolute;
        top: 50%;
        right: 42px;
        transform: translateY(-50%);
        font-size: 0;
    }


    &body{
        height: 0;
        overflow: hidden;
    }
}

.accordion{
    padding: 0;
    margin: 0;
    list-style: none;
    
    .active{
        .acc-{
            &item{
                button{
                    font-weight: $fw-bold;

                    &::after{
                        @extend .svg-minus;
                    }
                }
            }
            
            &body{
                padding: 10px 0 20px;
                height: auto;
                overflow: visible;
            }
        }
    }
}

.qm{
    @extend .svg;
    @extend .svg-bubble-qm;
}

// Video
.video{
    text-align: center;

    video{
        max-width: 880px;
    }
}

// Container No Padding
.no-pad{
    padding-left: 0;
    padding-right: 0;
}

// Buttons
.btn {
    position: relative;
    overflow: hidden;
    display: inline-flex;
    align-items: center;
    justify-content: center;
    height: $btn-h-default;
    padding: 0 50px;
    background-color: transparent;
    border: 1px solid transparent;
    border-radius: $btn-h-default * $btn-br-factor;
    font-family: $font1;
    @include font-set(body-small);
    color: $body-color;
    font-weight: $fw-normal;
    text-align: left;
    text-decoration: none;
    @include transitions($prop: background-color);
    outline: none;
    cursor: pointer;
    appearance: none;
    user-select: none;

    span{
        visibility: hidden;
    }

    &::before, &::after{
        content: attr(title);
        position: absolute;
        width: 100%;
        text-align: center;
        @include transitions($dur: 'now', $delay: .111s);
        text-indent: 0;
    }

    &::after{
        display: none;
    }

    &-primary {
        background-color: $c-primary;
        border-color: $c-primary;
        color: $c-white;
        &.selected {
            background-color: darken($c-primary, 5%);
            border-color: darken($c-primary, 5%);
            color: $c-white;
        }
    }

    &-light {
        background-color: $c-white;
        border-color: $c-white;
        color: $c-dark;
        &.selected {
            background-color: darken($c-white, 5%);
            border-color: darken($c-white, 5%);
            color: $c-dark;
        }
    }

    // Outline Buttons
    &-outline {
        &-primary {
            border-color: $c-primary;
            &.selected {
                background-color: $c-primary;
                color: $c-white;
            }
        }

        &-secondary {
            border-color: $c-primary;
            background-color: rgba($c-dark, 0.7);
            color: $c-white;
            &.selected {
                color: $c-white;
                background-color: $c-primary;
            }
        }
    
        &-light {
            border-color: $c-white;
            color: $c-white;
            &.selected {
                background-color: $c-white;
                color: $c-black;
            }
        }
    
        &-dark {
            border-color: $c-dark;
            color: $c-dark;
            &.selected {
                background-color: $c-dark;
                color: $c-white;
            }
            &.btn-selection{
                &.selected {
                    background-color: $c-primary;
                    border-color: $c-primary;
                    color: $c-white;

                    &[data-sel-title]::before, &[data-sel-title]::after{
                        content: attr(data-sel-title);
                    }
                }
            }
        }
    }

    &-badgy{
        i{
            font-style: normal;
            position: absolute;
            left: 6px;
            top: 50%;
            transform: translateY(-50%);

            .icon{
                font-size: 32px;
            }
        }
    }

    // Sizes
    &-small {
        height: $btn-h-small;
        padding: 0 30px;
        border-radius: $btn-h-small * $btn-br-factor;
    }

    &-narrow{
        padding: 0 25px;
    }

    // page top
    &-page-top {
        display: none;
        position: absolute;
        right: 20px;
        top: 96px;
        transform: rotate(180deg);
        border: 1px solid #474747;
        border-radius: 50%;
        padding: 3px;
    }

    // Disabled
    &-disabled{
        opacity: .4;
        pointer-events: none;
    }

    // page back
    &-page-back {
        margin-right: 10px;
    }

    // remove
    &-remove{
        display: inline-block;
        @extend .svg;
        @extend .svg-delete;
    }
}

// Price
.prc {
    &-first {
        text-decoration: line-through;
    }
}

// Fake Dropdown
.fake-dropdown{
    display: flex;
    align-items: center;
    border-bottom: 1px solid rgba($c-grey-border-dark, 0.5);
    padding: 20px 0 15px;
    width: 100%;
    &::after{
        content: "";
        display: inline-block;
        @extend .svg;
        @extend .svg-arrow-b;
    }
    .drp-content{
        padding-right: 50px;
        flex: 1;
        b{
            font-weight: $fw-bold;
            display: block;
            margin-bottom: 5px;
        }
    }
}

// Nav Tabs Fix
.nav{
    flex-wrap: nowrap;
    text-align: center;
    width: 100%;
    position: relative;

    &-link{
        display: flex;
        align-items: center;
        justify-content: center;
        text-align: center;
    }

    &-pills{
        padding: 0 15px;

        .nav-link{
            @include font-set(body-small);
            margin: 15px 5px;
            padding: 11px 22px;
            white-space: nowrap;
        }
    }

    &-tabs{
        .nav-link{
            @include font-set(h-tiny);
        }

        &:not(.tab-alt){
            box-shadow: inset 0px -3px 0px 0px $c-grey-light;

            .nav-link{
                border-bottom-width: 3px;
                border-bottom-color: transparent;
                white-space: nowrap;

                &.active{
                    border-bottom-color: $c-primary;
                }
            }
        }

        &.tab-alt{
            .nav-link{
                border-top-width: 4px;
                background-color: $c-secondary;
                color: $c-light;
                flex: 1;

                &.active{
                    border-top-color: $c-primary;
                    background-color: transparent;
                    color: $c-secondary;
                    @include transitions();
                }
            }
        }

        &.tab-ext{
            align-items: flex-end;
            box-shadow: none;
            .nav-link{
                border-top-width: 4px;
                background-color: $c-grey-border;
                color: $c-text-dark;
                padding: 0 15px;
                height: 40px;
                border: none;

                &.active{
                    background-color: $c-white;
                    color: $c-primary;
                    border-radius: 5px 5px 0 0;
                    padding: 0 20px 0;
                    height: 45px;
                }
                &:not(.active) + .nav-link{
                    margin-left: 1px;
                }
            }
        }
        @at-root {
            // Colored Theme
            .tab-ext-colored{
                .nav-tabs.tab-ext{
                    padding: 0 10px;
                    .nav-link{
                        background-color: $c-bg-light;
                        &.active{
                            background-color: $c-bg-colored;
                            color: $c-text-dark;
                        }
                    }
                }
                .tab-content{
                    .tab-pane{
                        padding: 30px 20px;
                        &.active{
                            background-color: $c-bg-colored;
                        }
                    }
                }
            }

            // Simple Theme
            .tab-simple{
                .nav-tabs{
                    box-shadow: none;
                    .nav-link{
                        border: none;
                        background-color: $c-white;
                        &.active{
                            background-color: $c-bg-light;
                            color: $c-text-dark;
                        }
                    }
                }
                .tab-content{
                    .tab-pane{
                        background-color: $c-bg-light;
                        padding: 30px 20px 20px;
                    }
                }
            }
        }
    }
}

.tab{
    &-pane{
        &.fade.show{
            opacity: 1;
        }
    }
}

// Page Cover
.cover{
    &-media{
        width: 100%;
        height: 100%;
        position: absolute;
        top: 0;
        left: 0;
        z-index: $z-lower;

        .responsive{
            position: absolute;
            height: 100%;
            top: 50%;
            left: 50%;
            transform: translate3d(-50%, -50%, 0);
            width: auto;
            overflow: visible;

            &::before{
                display: none;
            }
            
            img{
                height: 100%;
                position: static;
                width: auto;
                max-width: none;
            }
        }
    }

    &-body{
        position: relative;
        z-index: $z-low;
        width: 100%;
        text-align: center;
        opacity: 1;
        margin-top: auto;
        @include transitions($dur: 'normal', $func: 'eut');
        padding-bottom: 100px;

        h1{
            color: $c-text-light;
            @include font-set(h-large);
            margin-bottom: 0;
            display: inline-block;
            max-width: 210px;
        }
    }
}

.page-cover{
    position: relative;
    @include transitions($dur: 'normal', $func: 'eio');
    overflow: hidden;
    display: flex;

    &::before{
        content: "";
        display: block;
        width: 100%;
        height: 100%;
        position: absolute;
        top: 0;
        left: 0;
        z-index: $z-low;
        background-color: $c-black;
        opacity: .2;
    }

    .icon{
        position: absolute;
        bottom: 20px;
        left: calc(50% - 16px);
        color: $c-text-light;
        z-index: $z-low;
    }

    &.static{
        .responsive{
            width: auto;
            height: auto;
        }
    }
    &.cover-left{
        .cover-media .responsive{
            left: 0;
            transform: translate3d(0, -50%, 0);
        }
    }
    &.cover-right{
        left: auto;
        right: 0;
        transform: translate3d(0, -50%, 0);
    }
}

// Section Title
.section-title{
    @include font-set(h);
    text-align: center;
    margin: 50px auto 20px;
    max-width: 360px;
    padding: 0 20px;
}

.seo-text{
