}

// breadcrumb
.breadcrumb{
    padding: 15px 20px;
    display: flex;
    @include font-set(body-small);

    li{
        display: inline-flex;
        align-items: center;
        
        &:not(:last-child)::after{
            flex: none;
            content: "";
            display: inline-block;
            @extend .svg;
            @extend .svg-arrow2-r;
            transform: scale(.75);
        }

        + li {
            padding-left: 10px;
        }
    }

    &.brd-type-cover{
        position: absolute;
        z-index: $z-normal;

        li::after{
            @extend .svg-arrow2-r-w;
        }

        span{
            color: $c-text-light;
        }
    }
}

// Table
table {
    border-collapse: collapse;
    border-spacing: 0;
    width: 100%;
    th {
        font-weight: $fw-bold;
    }
    th,
    td {
        padding: 5px 10px;
    }
}

.table-responsive{
    display: block;
    width: 100%;
    overflow-x: auto;
    -webkit-overflow-scrolling: touch;
    td{
        white-space: nowrap;
    }
    th{
        vertical-align: top;
        text-align: center;
    }
}

// Content Loading
.content-loading {
