    &-suggest {
        display: none;
        border-radius: 5px;
        padding: 0 20px 15px;
        height: 100%;
        overflow: auto;

        @at-root {
            .spt-sug-row {
                display: none;
                padding: 20px 10px;
            
                time {
                    @include font-set(body-small);
                    color: $c-grey-dark;
                }
            
                &.prd-row {
                    padding: 10px 0;
                    .prd-row {
                        &-inner {
                            margin: 0;
                            padding: 0;
                        }
                        &-media {
                            width: 60px;
                        }
                    }
                }
            
                + .spt-sug-row {
                    border-top: 1px solid $c-grey-border;
                }

                &-empty {
                    padding: 40px 0;
                    > div {
                        + div {
                            margin-top: 40px;
                        }
                    }
                    .t {
                        @include font-set(body-large);
                        font-weight: $fw-bold;
                    }
                    .p {
                        margin-top: 10px;
                    }
                    .md {
                        margin-top: 10px;
                    }
                }
            }
        }
    }

    &.searching {
        .spt-sug-row {
            display: block;
            &-empty {
                display: none;
            }
        }
    }

    &-ready {
        overflow: hidden;
        #overlay {
            display: block;
        }
        .spt-search {
            bottom: auto;
            top: 0;
            height: 100vh;
            background: $c-bg-body;
            z-index: $z-high;
            
            .btn-back {
                display: block;
            }

            .btn-search{
                display: none;
            }
            
            &-input {
                background: $c-bg-light;
                input {
                    padding: 0;
                }
            }
            &-suggest {
                display: block;
            }
            &-kws{
                display: none;
            }
        }
    }
}

// Support Article Pages
.spt{
    &-header{
        background-color: $c-bg-colored;
        .breadcrumb{
            display: none;
        }
    }
    &-hdr{
        &-inner{
            height: 150px;
            width: 100%;
            position: relative;
        }
        &-media{
            width: 230px;
            height: 100%;
            overflow: hidden;
            position: absolute;
            right: 0;
            top: 0;
            figure{
                width: 100%;
            }
            img{
                width: 100%;
            }
        }
        &-content{
            display: flex;
            align-items: center;
            padding: 10px 0 0 15px;
            .btn-spt-back{
                margin-right: 10px;
                display: inline-block;
            }
        }
        &-title{
            margin: 0;
            @include font-set(h-xsmall);
        }
    }
    &-article{
        &-filters{
            .nav-link{
                flex: 1;
            }
        }
        &-list{
            padding: 25px 20px;
        }
        &-header{
            height: 50px;
            padding: 9px 15px;
            .btn-spt-back{
                display: inline-block;
            }
            .breadcrumb{
                display: none;
            }
        }
        &-content{
            margin: 0 auto;
            max-width: 780px;
            padding: 0 30px;
            p{ 
                margin: 0;
                + p{
                    margin-top: 20px;
                }
                + .spt-article-subtitle{
                    margin-top: 40px;
                }
            }
        }
        &-title{
            @include font-set(h);
            margin-bottom: 20px;
        }
        &-video{
            margin: 40px 0;
            video{
                width: 100%;
            }
        }
        &-subtitle{
            margin-bottom: 20px;
            @include font-set(h-paragraph);
        }
        &-tags{
            margin-top: 40px;
            padding: 0 30px;
            a{
                @include font-set(body-small);
                color: $c-grey-dark;
            }
        }
        &-feedback{
            margin: 30px 0 45px;
            padding: 0 30px;
            span{
                display: block;
                margin-bottom: 15px;
            }
            .btn + .btn{
                margin-left: 10px;
            }
        }
    }
    &-video{
        &-info{
            margin-top: 5px;
            padding: 0 30px;
            color: $c-grey-dark;
        }
    }
    &-list{
        &-info{
            @include font-set(body-small);
            margin-bottom: 25px;
            padding-left: 10px;
        }
        &-title{
            text-align: center;
            @include font-set(h);
            margin-bottom: 20px;
        }
        &-item{
            border-top: 1px solid $c-grey-light;
            position: relative;
            a{
                display: block;
                padding: 20px 10px;
                font-weight: $fw-bold;
            }
            &.spt-item-video a::after{
                position: absolute;
                top: calc(50% - 16px);
                right: 0;
                content: "";
                display: inline-block;
                @extend .svg;
                @extend .svg-video-type-dark;
            }
        }
    }
    &-bnf{
        &-title{
            margin-bottom: 20px;
            @include font-set(h);
            text-align: center;
        }
        &-slider{
            background-color: $c-bg-colored;
            padding: 40px 0;
            margin-top: 45px;
            .spt-bnf {
                flex: 1;
                display: flex;
                flex-direction: column;
                align-items: center;
                text-align: center;
                padding: 30px 20px 40px;
                border-radius: $br-popup-middle;
                height: 100%;
                .icon {
                    position: static;
                }
                .t {
                    margin-top: 40px;
                }
                .b {
                    margin-top: auto;
                    padding-top: 50px;
                }
            }
            .swiper-slide{
                width: 220px;
                height: auto;
                + .swiper-slide{
                    margin-left: 10px;
                }
                &:first-child{
                    margin-left: 20px;
                }
                &:last-child{
                    margin-right: 20px;
                }
            }
            .swiper-button-next, .swiper-button-prev{
                display: none;
            }
        }
    }
}


